@charset "UTF-8";
@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PT-Mono.woff2") format("woff2"), url("../fonts/PT-Mono.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Roboto-Condensed.woff2") format("woff2"), url("../fonts/Roboto-Condensed.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/Roboto-Condensed-Bold.woff2") format("woff2"), url("../fonts/Roboto-Condensed-Bold.woff2") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  scroll-behavior: smooth;
  height: 100%;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #f2f5f6;
}

body {
  width: 100%;
  height: 100%;
  background-color: #041827;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

button {
  padding: 0;
  text-transform: uppercase;
  border-radius: 3px;
  border: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  cursor: pointer;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.container {
  width: 100%;
  max-width: 1220px;
  margin: 0 auto;
  padding: 0 60px;
}

.logo > a {
  display: flex;
  text-decoration: none;
  outline: none;
}
.logo > a:hover, .logo > a:focus-visible {
  opacity: 0.7;
}
.logo > a:active {
  opacity: 0.5;
}

.logo__icon {
  margin-right: 15px;
}

.logo__title-container {
  display: flex;
  flex-direction: column;
}

.logo__title-container h2 {
  min-width: 145px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 25px;
  color: #f2f5f6;
}

.logo__title-container span {
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 500;
  font-size: 12px;
  color: rgba(255, 255, 255, 0.7);
}

.field input,
.field textarea {
  font-size: 16px;
  box-sizing: border-box;
  display: block;
  border-radius: 3px;
  border: 0;
  background-color: rgba(4, 20, 39, 0.15);
  color: #ffffff;
  outline: none;
}
.field input::placeholder,
.field textarea::placeholder {
  color: #ffffff;
  opacity: 1;
}
.field input:hover, .field input:focus-visible,
.field textarea:hover,
.field textarea:focus-visible {
  box-shadow: 0 0 0 2px #041827;
}

.field input {
  height: 50px;
  padding: 15px;
}

.field textarea {
  height: 155px;
  resize: none;
  margin: 20px 0 0;
  padding: 25px 15px;
}

.checkbox {
  position: relative;
  box-sizing: border-box;
  font-family: "PT Mono", monospace;
  font-size: 14px;
  line-height: 16px;
}
.checkbox:hover .checkbox__mark, .checkbox:focus-visible .checkbox__mark {
  border-color: #041827;
}
.checkbox:active .checkbox__mark {
  border-color: #041827;
  opacity: 0.7;
}

.checkbox input {
  display: none;
}

.checkbox__mark {
  position: absolute;
  width: 15px;
  height: 15px;
  border-radius: 2px;
  border: 1px solid #ffffff;
  left: 0;
  top: 50%;
  bottom: 50%;
  margin: auto;
}

.checkbox input:checked ~ .checkbox__mark::after {
  content: "";
  position: absolute;
  top: 2px;
  left: 5px;
  width: 4px;
  height: 7px;
  border-bottom: 1px solid #ffffff;
  border-right: 1px solid #ffffff;
  transform: rotate(45deg);
}

.header {
  position: fixed;
  top: 0;
  z-index: 3;
  width: 100%;
  min-height: 120px;
  background: linear-gradient(180deg, #041827 84.9%, rgba(4, 24, 39, 0) 100%);
}
@media (max-width: 1023px) {
  .header {
    position: static;
    min-height: 100px;
  }
}
@media (max-width: 767px) {
  .header {
    min-height: 70px;
  }
}

.header__container {
  width: auto;
  box-sizing: content-box;
  display: flex;
  justify-content: space-between;
  max-width: 1160px;
  padding: 0 60px;
}
@media (max-width: 1023px) {
  .header__container {
    width: 100%;
    padding: 20px 70px 0 50px;
    box-sizing: border-box;
    justify-content: space-between;
  }
}
@media (max-width: 767px) {
  .header__container {
    min-width: 320px;
    padding: 10px 35px 0 26px;
  }
}

.header__container > a {
  margin-right: 30px;
}
@media (max-width: 1023px) {
  .header__container > a {
    margin: 0;
  }
}

.header__logo-icon {
  margin-top: 30px;
}
@media (max-width: 1023px) {
  .header__logo-icon {
    margin-top: 0;
  }
}
@media (max-width: 767px) {
  .header__logo-icon {
    width: 35px;
    height: 35px;
    margin: 5px 0 0;
  }
}

.header__logo-title-container h2 {
  height: 25px;
  margin: 35px 0 5px;
}
@media (max-width: 1023px) {
  .header__logo-title-container h2 {
    margin-top: 5px;
  }
}
@media (max-width: 767px) {
  .header__logo-title-container h2 {
    display: none;
  }
}

@media (max-width: 767px) {
  .header__logo-title-container span {
    display: none;
  }
}

.nav {
  display: flex;
  justify-content: space-between;
  width: 100%;
  max-width: 785px;
}
@media (max-width: 1023px) {
  .nav {
    width: auto;
  }
}

.nav__list {
  display: grid;
  gap: 30px;
  margin: 45px 0 0 0;
  padding: 0;
  list-style-type: none;
  grid-template-columns: repeat(2, 225px);
}
@media (max-width: 1023px) {
  .nav__list {
    gap: 20px;
    margin: 0;
    grid-template-columns: repeat(2, 40px);
  }
}

.nav__list-item a {
  display: flex;
  font-family: "PT Mono", monospace;
  text-decoration: none;
  color: #f2f5f6;
  font-size: 16px;
}
.nav__list-item a:hover, .nav__list-item a:focus-visible {
  opacity: 0.7;
  outline: none;
}
.nav__list-item a:active {
  opacity: 0.5;
}

@media (max-width: 1023px) {
  .nav__list-item span {
    display: none;
  }
}

.nav__list-icon {
  margin-right: 15px;
}
@media (max-width: 1023px) {
  .nav__list-icon {
    margin: 0;
    padding: 10px;
  }
}

.nav__button {
  width: 160px;
  height: 40px;
  padding: 10px 15px;
  margin-top: 35px;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  font-weight: 700;
  background-color: transparent;
  text-transform: uppercase;
  color: #f2f5f6;
  box-shadow: 0 0 0 1px #f2f5f6 inset;
}
.nav__button:hover, .nav__button:focus-visible {
  opacity: 0.7;
}
.nav__button:active {
  opacity: 0.5;
}
@media (max-width: 1023px) {
  .nav__button {
    display: none;
  }
}

.main {
  position: relative;
  min-height: 880px;
}
.main::before {
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(25, 32, 36, 0.7) 71.72%, rgba(25, 32, 36, 0) 100%);
  z-index: 1;
}
@media (max-width: 1023px) {
  .main {
    min-height: 640px;
  }
}
@media (max-width: 767px) {
  .main {
    min-height: 560px;
  }
}

.main__background {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
}

.main__container {
  position: relative;
  z-index: 2;
  box-sizing: border-box;
  padding: 305px 125px 280px;
}
@media (max-width: 1023px) {
  .main__container {
    max-width: 767px;
    padding: 180px 50px 205px;
  }
}
@media (max-width: 767px) {
  .main__container {
    width: 320px;
    padding: 150px 25px;
  }
}

.main h1 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 80px;
  line-height: 90px;
  font-weight: 700;
  letter-spacing: 0.07em;
  color: #f2f5f6;
  margin: 0 0 40px;
  text-transform: uppercase;
}
@media (max-width: 1023px) {
  .main h1 {
    height: 60px;
    line-height: 75px;
    font-size: 64px;
    margin-bottom: 20px;
  }
}
@media (max-width: 767px) {
  .main h1 {
    font-size: 36px;
    line-height: 42px;
    height: 75px;
  }
}

.main h2 {
  max-width: 685px;
  min-height: 70px;
  font-family: "PT Mono", monospace;
  font-weight: 400;
  line-height: 34px;
  font-size: 22px;
  color: #f2f5f6;
  margin: 0 0 40px;
}
@media (max-width: 1023px) {
  .main h2 {
    max-width: 670px;
    margin-bottom: 50px;
  }
}
@media (max-width: 767px) {
  .main h2 {
    font-size: 14px;
    line-height: 22px;
    min-height: 35px;
  }
}

.main a {
  position: relative;
  display: block;
  box-sizing: border-box;
  text-align: center;
  text-transform: uppercase;
  border-radius: 3px;
  background-color: #00b2ff;
  width: 410px;
  height: 55px;
  padding: 15px 25px;
}
.main a:hover, .main a:focus-visible {
  opacity: 0.7;
  outline: none;
}
.main a:active {
  opacity: 0.5;
}
.main a::after {
  content: "Получить бесплатную консультацию";
  position: absolute;
  display: block;
  top: 16px;
  left: 25px;
  width: 360px;
  min-height: 20px;
  font-weight: 700;
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0.05em;
  color: #f2f5f6;
}
@media (max-width: 1023px) {
  .main a {
    margin-left: 45px;
  }
}
@media (max-width: 767px) {
  .main a {
    width: 270px;
    height: 40px;
    padding: 10px 30px;
    margin: 0;
    font-size: 14px;
    line-height: 16px;
  }
  .main a::after {
    content: "бесплатная консультация";
    font-size: 14px;
    line-height: 16px;
    top: 12px;
    left: 30px;
    width: 210px;
  }
}

.advantages {
  background-color: rgb(248, 253, 255);
}

.advantages__list {
  max-width: 1220px;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 30px;
  margin: 0 auto;
  padding: 120px 0 120px;
}
@media (max-width: 1023px) {
  .advantages__list {
    display: grid;
    grid-template-columns: repeat(2, 320px);
    padding: 80px 50px 80px;
  }
}
@media (max-width: 767px) {
  .advantages__list {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding: 50px 25px;
  }
}

.advantages__list-item {
  position: relative;
  box-sizing: border-box;
  width: 595px;
  min-height: 300px;
  padding: 94px 44px 34px 45px;
  border: 1px solid #ebebeb;
  border-left: none;
  box-shadow: inset 5px 0 #00b2ff;
  cursor: default;
}
.advantages__list-item:hover {
  background-color: #00b2ff;
  border-color: #00b2ff;
}
@media (max-width: 1023px) {
  .advantages__list-item {
    width: 320px;
    min-height: 360px;
    padding: 94px 29px 34px 45px;
  }
}
@media (max-width: 767px) {
  .advantages__list-item {
    width: 270px;
    min-height: 340px;
    padding: 79px 29px 34px 30px;
  }
}

.advantages__list-item-icon {
  fill: #00b2ff;
  position: absolute;
  top: 0;
  left: 0;
}
.advantages__list-item-icon--stopwatch {
  top: 39px;
  left: 45px;
}
.advantages__list-item-icon--hired {
  top: 39px;
  left: 45px;
}
.advantages__list-item-icon--detective {
  top: 39px;
  left: 40px;
}
.advantages__list-item-icon--tag {
  top: 39px;
  left: 45px;
}
@media (max-width: 767px) {
  .advantages__list-item-icon--stopwatch, .advantages__list-item-icon--hired, .advantages__list-item-icon--detective, .advantages__list-item-icon--tag {
    top: 30px;
    left: 30px;
    width: 30px;
    height: 30px;
  }
}

.advantages__list-item h3 {
  color: #00b2ff;
  margin: 0 0 15px;
  font-size: 24px;
  font-weight: 400;
  line-height: 35px;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .advantages__list-item h3 {
    line-height: 30px;
  }
}
@media (max-width: 767px) {
  .advantages__list-item h3 {
    font-size: 20px;
    line-height: 28px;
  }
}

.advantages__list-item p {
  font-family: "PT Mono", monospace;
  font-size: 16px;
  line-height: 21px;
  color: #041827;
  margin: 0;
}
@media (max-width: 1023px) {
  .advantages__list-item p {
    width: 245px;
  }
}
@media (max-width: 767px) {
  .advantages__list-item p {
    width: 210px;
    font-size: 14px;
    line-height: 19px;
  }
}

.advantages__list-item--primary .advantages__list-item-description {
  color: #f2f5f6;
}

.advantages__list-item:hover .advantages__list-item-icon,
.advantages__list-item:hover h3,
.advantages__list-item:hover p {
  fill: #f2f5f6;
  color: #f2f5f6;
}

.about {
  background-color: rgb(248, 253, 255);
  padding-bottom: 100px;
}
@media (max-width: 1023px) {
  .about {
    padding-bottom: 80px;
  }
}

.about__container {
  position: relative;
  box-sizing: border-box;
  padding: 5px 30px 20px 250px;
  min-height: 320px;
}
@media (max-width: 1023px) {
  .about__container {
    width: 670px;
    min-height: 380px;
    padding: 0 0 0 175px;
  }
}
@media (max-width: 767px) {
  .about__container {
    max-width: 480px;
    width: 100%;
    min-height: 305px;
    padding: 0 25px 0;
  }
}

.about__background {
  position: absolute;
  left: 0;
  top: 0;
}
@media (max-width: 767px) {
  .about__background {
    display: none;
  }
}

.about h2 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  color: #041827;
  font-size: 36px;
  line-height: 120%;
  height: 45px;
  font-weight: 400;
  margin: 0 0 15px;
}
@media (max-width: 767px) {
  .about h2 {
    font-size: 28px;
    line-height: 39px;
    height: 35px;
  }
}

.about__description-container {
  min-height: 170px;
  margin-bottom: 20px;
}
@media (max-width: 767px) {
  .about__description-container {
    min-height: auto;
  }
}

@media (max-width: 767px) {
  .about__accordion-container::before {
    display: block;
    font-family: "PT Mono", monospace;
    content: "Печатные платы и комплектующие Service Devices применяются на предприятиях Российских Железных Дорог (РЖД), РоссАвтоПрома (ВАЗ, АвтоГАЗ), МинАтома, СпецМедТехники. Среди наших клиентов крупнейшие Производители светотехники России.";
    font-size: 14px;
    line-height: 19px;
    color: #041827;
    margin: 0 0 10px;
  }
}

.about p {
  font-family: "PT Mono", monospace;
  font-size: 16px;
  line-height: 120%;
  color: #041827;
  margin: 0 0 20px;
}
@media (max-width: 767px) {
  .about p {
    font-size: 14px;
    line-height: 19px;
    margin-bottom: 10px;
  }
}

@media (max-width: 767px) {
  .about__description-mobile {
    display: none;
  }
}

.about__button {
  display: block;
  width: 180px;
  height: 45px;
  padding: 14px 12px;
  font-size: 14px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.05em;
  color: #00b2ff;
  background-color: rgb(248, 253, 255);
  border: 1px solid #00b2ff;
}
@media (max-width: 767px) {
  .about__button {
    width: 160px;
    height: 35px;
    padding: 8px 15px;
    margin: 0 auto;
  }
}
.about__button:hover, .about__button:focus-visible {
  background-color: #00b2ff;
  color: rgb(248, 253, 255);
}
.about__button:active {
  opacity: 0.7;
}

.products__container {
  box-sizing: border-box;
  max-width: 1280px;
  padding: 70px 30px 140px;
}
@media (max-width: 1023px) {
  .products__container {
    width: 768px;
    padding: 60px 50px 75px;
  }
}
@media (max-width: 767px) {
  .products__container {
    width: 320px;
    padding: 50px 25px 86px;
  }
}

.products h2 {
  margin: 0 0 50px;
  font-size: 36px;
  line-height: 50px;
  color: #ffffff;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-weight: 400;
}
@media (max-width: 1023px) {
  .products h2 {
    height: 85px;
  }
}
@media (max-width: 767px) {
  .products h2 {
    display: none;
  }
}

.products .products__title-mobile {
  display: none;
}
@media (max-width: 767px) {
  .products .products__title-mobile {
    color: #ffffff;
    font-family: "Roboto Condensed", "Arial", sans-serif;
    font-weight: 400;
    display: block;
    font-size: 28px;
    line-height: 39px;
    margin: 0 0 40px;
    height: 64px;
  }
}

.products-list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  justify-content: space-between;
  display: flex;
  gap: 30px 85px;
}
@media (max-width: 1023px) {
  .products-list {
    flex-direction: column;
    align-items: center;
    flex-wrap: wrap;
  }
}

.products-list__item {
  width: 100%;
}

.products-list__item a {
  display: block;
  position: relative;
  box-sizing: border-box;
  max-width: 350px;
  min-height: 650px;
  padding: 50px 0 0 40px;
  text-decoration: none;
}
@media (max-width: 1023px) {
  .products-list__item a {
    max-width: 100%;
    min-height: 250px;
    padding: 40px 45px 0;
  }
}
@media (max-width: 767px) {
  .products-list__item a {
    padding: 30px 30px 0;
    width: 270px;
  }
}
.products-list__item a:hover, .products-list__item a:focus-visible {
  opacity: 0.7;
  outline: 2px solid rgba(255, 255, 255, 0.7);
}
.products-list__item a:active {
  opacity: 0.5;
}

.products-list__item-background {
  z-index: -1;
  position: absolute;
  top: 50%;
  left: 50%;
  width: 100%;
  height: 100%;
  min-width: 100%;
  min-height: 100%;
  object-fit: cover;
  transform: translate(-50%, -50%);
}

.products-list__item h3 {
  width: 230px;
  height: 55px;
  margin: 0;
  font-size: 20px;
  color: #ffffff;
  font-weight: 400;
  line-height: 30px;
}
@media (max-width: 767px) {
  .products-list__item h3 {
    width: 150px;
    font-size: 18px;
    height: 50px;
  }
}

.products-list__item-button {
  display: flex;
  justify-content: center;
  align-items: center;
  position: absolute;
  bottom: 0;
  right: 0;
  width: 60px;
  height: 60px;
  background-color: #00b2ff;
  border-radius: 3px 0 0 0;
}
@media (max-width: 767px) {
  .products-list__item-button {
    width: 40px;
    height: 40px;
  }
}
.products-list__item-button:focus-visible {
  opacity: 0.5;
}

@media (max-width: 767px) {
  .products-list__item-icon {
    width: 6px;
    height: 6px;
  }
}

.feedback {
  box-sizing: border-box;
  padding: 170px 0;
